<template>
  <h3>Профиль {{ userFullName }}</h3>
  <div class="row">
    <div class="col s12">
      <div class="card-panel">
        <div class="row">
          <div class="col s12 m6">
            <p><strong>Ваш ID</strong></p>
            <p>{{ userInfo.id }}</p>
          </div>
          <div v-if="userInfo.recordsId !== ''" class="col s12 m6">
            <p><strong>ID Вашей базы записей</strong></p>
            <p>{{ userInfo.recordsId }}</p>
          </div>
        </div>
        <div class="row">
          <div class="col s12 m6">
            <p><strong>Ваше имя</strong></p>
            <p>{{ userInfo.firstName }}</p>
          </div>
          <div class="col s12 m6">
            <p><strong>Ваша фамилия</strong></p>
            <p>{{ userInfo.lastName }}</p>
          </div>
        </div>
        <div class="row">
          <div class="col s12 m6">
            <p><strong>Ваш адрес электронной почты</strong></p>
            <p>{{ userInfo.email }}</p>
          </div>
          <button class="btn col s12 m3 l2 red darken-4"
                  name="action"
                  @click.prevent="logout">Выйти
            <i class="material-icons right">power_settings_new</i>
          </button>
        </div>
      </div>
    </div>
  </div>
  <div v-if="userInfo.recordsId === ''" class="row">
    <div class="col s12">
      <div class="card-panel">
        <form @submit.prevent="attachBase">
          <div class="row">
            <div class="input-field col s6">
              <input id="records_id" v-model.trim="state.recordsId"
                     :class="{ invalid: isRecordsIdRequiredInvalid || isRecordsIdMinLengthInvalid }" type="text">
              <label for="records_id">Введите ID прикрепляемой базы записей</label>
              <!--suppress TypeScriptUnresolvedReference, JSUnresolvedReference -->
              <small
                v-if="isRecordsIdRequiredInvalid"
                class="helper-text red-text"
              >{{ v$.recordsId.required.$message }}</small>
              <!--suppress TypeScriptUnresolvedReference, JSUnresolvedReference -->
              <small
                v-else-if="isRecordsIdMinLengthInvalid"
                class="helper-text red-text"
              >{{ v$.recordsId.minLength.$message }}</small>
            </div>
          </div>

          <div class="row">
            <button class="btn col s12 m3 green darken-1" name="action" type="submit">Прикрепить базу записей
              <i class="material-icons right">cloud_download</i>
            </button>
            <button class="btn col s12 m3 offset-m6 green darken-4"
                    name="action"
                    @click.prevent="createNewUserBase"
            >Создать новую базу записей
              <i class="material-icons right">add_circle_outline</i>
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { useAuthStore } from '@/stores/auth';
import { useDbStore } from '@/stores/database';
import { useVuelidate } from '@vuelidate/core';
import { required, minLength, helpers } from '@vuelidate/validators';
import { computed, reactive, ref } from 'vue';
import { storeToRefs } from 'pinia';

const { userInfo, recordsIdAttach, userFullName } = storeToRefs(useDbStore());
const { attachUserBase, createNewUserBase } = useDbStore();

const { logout } = useAuthStore();

const minRecordsIdLength = ref(20);
const state = reactive({
  recordsId: ''
});
const rules = {
  recordsId: {
    required: helpers.withMessage(
      'Поле ID является обязательным.',
      required
    ),
    minLength: helpers.withMessage(
      ({ $params }) =>
        `Длина ID базы записей должна быть не менее, чем ${ $params.min } символов.`,
      minLength(minRecordsIdLength)
    )
  }
};
const isRecordsIdRequiredInvalid = computed(() => {
  return v$.value.recordsId.$dirty && v$.value.recordsId.required.$invalid;
});
const isRecordsIdMinLengthInvalid = computed(() => {
  return v$.value.recordsId.$dirty && v$.value.recordsId.minLength.$invalid;
});
const v$ = useVuelidate(rules, state);

const attachBase = async () => {
  if (v$.value.$invalid) {
    v$.value.$touch();
    return;
  }
  recordsIdAttach.value = state.recordsId;

  await attachUserBase();
};
</script>

<style lang="scss" scoped>
.mc-form-profile {
  padding: 32px;
}
</style>